const PopUp = ({ cardId }) => {
  const [isOpen, setIsOpen] = useState(false);

  const openPopup = () => {
    setIsOpen(true);
  };

  const closePopup = () => {
    setIsOpen(false);
  };


  const selectedData = popUpData.find(item => item.id === cardId);

  if (!selectedData) return null;

  const { title, description, area, address, images, text } = selectedData;

  return (
    <div className='popUp__item'>
      <div className="popUp__text">
        <div className="popUp__text__title">
          <h3>{title}</h3>
          <p>{description}</p>
        </div>
        <div className="popUp__text__accorgion">
        <div className="container">
          <button onClick={openPopup}>read more <img src={imgArrow} alt="" /></button>
          {isOpen && (
            <div className="popup">
              <span className="close" onClick={closePopup}>&times;</span>
              <div className="scrollable-content">
                <p>{text}</p>
              </div>
            </div>
          )}
          </div>
        </div>
        <div className="popUp__text__subtitle">
          <h4>Площадь:<br />Адрес:</h4>
          <h5>{area}<br />{address}</h5>
        </div>
      </div>
      <div className="popUp__images">
        <PopUpSwiper images={images} />
      </div>
    </div>
  );
};

const PopUpSwiper = ({ images }) => {
  return (
    <Swiper
      modules={[A11y]}
      spaceBetween={50}
      slidesPerView={1}
      loop={true}
      speed={600}
    >
      {images.map((item, index) => (
        <SwiperSlide key={index}>
          <div className="swiperPopUp__item">
            <img src={item.img} alt="" />
          </div>
        </SwiperSlide>
      ))}
      <SwiperNavigations />
    </Swiper>
  );
};

const SwiperNavigations = () => {
  const swiper = useSwiper();

  return (
    <div className="navigation-btns nav__button">
      <button onClick={() => swiper.slideNext()} ><img src={imgArrow1} alt="" /></button>
    </div>
  );
};


export default PopUp;





